<template>
  <div class="swap">
    <HeadTop title="兑换" :isShowTop="true" :isSubHeader="false"></HeadTop>
    <div class="swap-item">
        <div class="title">USDT 兑换 KTT</div>
          <van-field
    v-model="swapKttAmount"
    center
    clearable
    label=""
    @update:model-value="getOutAmount"
    placeholder="请输入兑换数量"
  >
    <template #extra>
      <span class="sympol">KTT</span>
    </template>
  </van-field>
  <div class="flex-between">
    <div class="price-desc">需要： <span>{{usdtAmount}}</span> USDT</div>
    <div class="price-desc">余额： <span>{{usdtBalance}}</span> USDT</div>
  </div>
  
  <div class="button-box">
     <van-button type="primary" :loading="btn.loading" size="large" loading-text="兑换中..." block v-if="Number(usdtAllowance > 0)" @click="swapByUsdt">兑换</van-button>
     <van-button type="primary" :loading="btn.loading" v-else size="large" loading-text="正在授权..." @click="approveUsdt">授权USDT</van-button>
  </div>
    </div>

      <!-- <div class="swap-item">
        <div class="title">KTT 兑换 KTM</div>
          <van-field
    v-model="swapAmount"
    center
    clearable
    label=""
    @update:model-value="getOutKtmAmount"
    placeholder="请输入兑换数量"
  >
    <template #extra>
      <span class="sympol">KTT</span>
    </template>
  </van-field>
  <div class="flex-between">
    <div class="price-desc">获得: <span>{{ktmAmount}}</span> KTM</div>
    <div class="price-desc">余额: <span>{{kttBalance}}</span> KTT</div>
  </div>
  
  <div class="button-box">
    <van-button color="linear-gradient(90deg, #5161F5 0%, #64C5FA 100%)" type="primary" :loading="btn.swaploading" size="large" loading-text="兑换中..." block v-if="Number(kttAllowance) > 0" @click="swapKtm">兑换</van-button>
     <van-button color="linear-gradient(90deg, #5161F5 0%, #64C5FA 100%)" type="primary" :loading="btn.swaploading" v-else size="large" loading-text="正在授权..." @click="approveKtt">授权KTT</van-button>
  </div>
    </div> -->
  </div>
</template>
<script>
import HeadTop from '@/components/HeadTop'
import { reactive, toRefs, computed, watch } from "vue";
import { useStore } from "vuex";
import { MaxUint256 } from "@ethersproject/constants";
import BigNumber from 'bignumber.js';
import _ from 'lodash'
import swapAbi from "@/utils/swapAbi";
import tokenAbi from "@/utils/abi";
import config from "@/utils/config";
import { Toast } from 'vant';
export default {
  components: {
    HeadTop
  },
  setup () {
    const store = useStore()
    const data = reactive({
      address: computed(() => store.state.accounts[0]),
      web3: computed(() => store.state.provider),
      swapContract: null,
      usdtContract: null,
      KttContract: null,
      KtmContract: null,
      usdtAllowance: '',
      kttAllowance: '',
      usdtBalance: '0.00',
      kttBalance: '0.00',
      usdtAmount: '0.00',
      kttAmount: '0.00',
      ktmAmount: '0.00',
       btn: {
        loading: false,
        swaploading: false
      },
      swapKttAmount: '',
      swapAmount: '',
      pickerValue: {
          text: '显示器',
          value: 1 
      },
      columns: [
        {
          text: '显示器',
          value: 1,   
        },
        {
          text: '主机',
          value: 2
        },
        {
          text: '主播',
          value: 3
        },
        {
          text: '麦克风',
          value: 4
        },
        {
          text: '声卡',
          value: 5
        }
      ],
      initContract: () => {
        data.swapContract = new data.web3.eth.Contract(swapAbi, config.swapAddress)
        data.usdtContract = new data.web3.eth.Contract(tokenAbi, config.usdtAddress)
        data.kttContract = new data.web3.eth.Contract(tokenAbi, config.kttAddress)
        data.ktmContract = new data.web3.eth.Contract(tokenAbi, config.ktmAddress)
        data.initData()
      },
      initData: () => {
        data.getBalance()
        data.getUsdtAllowance()
        data.getKttAllowance()
      },
      getBalance: async () => {
        let kttDecimals = await data.kttContract.methods.decimals().call()
        let usdtDecimals = await data.usdtContract.methods.decimals().call()
        let kttBalance = await data.kttContract.methods.balanceOf(data.address).call()
        let usdtBalance = await data.usdtContract.methods.balanceOf(data.address).call()
        data.kttBalance =  new BigNumber(kttBalance).dividedBy(10 ** kttDecimals).toFixed(2)
        data.usdtBalance =  new BigNumber(usdtBalance).dividedBy(10 ** usdtDecimals).toFixed(2)
      },
      getUsdtAllowance: async () => {
        let allowance = await data.usdtContract.methods.allowance(data.address, config.swapAddress).call()
        data.usdtAllowance = allowance;
      },
       getKttAllowance: async () => {
        let allowance = await data.kttContract.methods.allowance(data.address, config.swapAddress).call()
        data.kttAllowance = allowance;
      },
      approveUsdt: async () => {
        data.btn.loading = true
        try {
          await data.usdtContract.methods.approve(config.swapAddress, MaxUint256).send({from: data.address})
          Toast('授权成功')
          data.getUsdtAllowance()
          data.btn.loading = false
        } catch (error) {
          Toast('授权失败')
          data.btn.loading = false
        }
        
      },
      approveKtt: async () => {
        data.btn.swaploading = true
        try {
          await data.kttContract.methods.approve(config.swapAddress, MaxUint256).send({from: data.address})
          Toast('授权成功')
          data.getKttAllowance()
          data.btn.swaploading = false
        } catch (error) {
          Toast('授权失败')
          data.btn.swaploading = false
        }
        
      },
      swapByUsdt: async () => {
        if(!data.swapKttAmount) {
          Toast('请先输入数量')
          return
        }
        if(Number(data.usdtAmount) > Number(data.usdtBalance)) {
          Toast('USDT余额不足')
          return
        }
        
         data.btn.loading = true
        try {
          let decimals = await data.kttContract.methods.decimals().call()
          let amount = new BigNumber(data.swapKttAmount).multipliedBy(10 ** decimals).toFixed()
          await data.swapContract.methods.swapByUsdt(amount).send({from: data.address})
          Toast('兑换成功')
          data.getBalance()
          data.btn.loading = false
        } catch (error) {
          Toast('兑换失败')
          data.btn.loading = false
        }
      },
      swapKtm: async () => {
        if(!data.swapAmount) {
          Toast('请先输入数量')
          return
        }
        if(Number(data.swapAmount) > Number(data.kttBalance)) {
          Toast('KTT余额不足')
          return
        }
         data.btn.swaploading = true
          let decimals = await data.kttContract.methods.decimals().call()
          let amount = new BigNumber(data.swapAmount).multipliedBy(10 ** decimals).toFixed()
        try {
          await data.swapContract.methods.swapKtm(amount).send({from: data.address})
          Toast('兑换成功')
           data.initData()
          data.btn.swaploading = false
        } catch (error) {
          Toast('兑换失败')
          data.btn.swaploading = false
        }
      },
      
      getOutAmount: _.throttle(async function(){
        if (!data.swapKttAmount || Number(data.swapKttAmount)  == 0) {
          return '0.00'
        }
        let decimals = await data.kttContract.methods.decimals().call()
        let amount = new BigNumber(data.swapKttAmount).multipliedBy(10 ** decimals).toFixed()
        let outAmount = await data.swapContract.methods.getUsdtAmount(amount).call()
        let usdtDecimals = await data.usdtContract.methods.decimals().call()
        let usdtAmount = new BigNumber(outAmount).dividedBy(10 ** usdtDecimals).toFixed(2)
        data.usdtAmount = usdtAmount
      }, 1000),
      getOutKtmAmount: _.throttle(async function(){
        if (!data.swapAmount || Number(data.swapAmount)  == 0) {
          data.ktmAmount = '0.00'
          return '0.00'
        }
        let kttDecimals = await data.kttContract.methods.decimals().call()
         let ktmDecimals = await data.ktmContract.methods.decimals().call()
         let amount = new BigNumber(data.swapAmount).multipliedBy(10 ** kttDecimals).toFixed()
        let price = await data.swapContract.methods.getKtmAmount(amount).call()
        data.ktmAmount = new BigNumber(price).dividedBy(10**ktmDecimals).toFixed(2)
      }, 1000)
    })
    if (data.address) {
      data.initContract()
    }
    watch(() => data.address, (val) => {
      if (val) {
        data.initContract()
      }
    })
    const refData = toRefs(data);
    return {
      ...refData
    };
  }
}
</script>
<style lang="less" scoped>
.swap{
  padding: 16px;
}
.sympol{
    font-size: 14px;
}
.swap-item{
  background: #3F2E6F;
  border-radius: 5px;
  padding: 20px 14px;
  margin-bottom: 15px;
  .title{
    text-align: center;
    color: #fff;
    font-size: 16px;
    font-weight: 600;
    margin-bottom: 20px;
  }
  .price-desc{
    font-size: 12px;
    font-weight: 600;
    color: #E4D7FF;
    margin: 15px 8px;
    span{
      color: #FFD511;
    }
  }
}
</style>
